export function getConfigByHostname() {
  const hostname =  window.location.hostname;
  if (liveDomains.includes(hostname)) {
    return configs.livePantheon;
  } else if (hostname.match(/dev-[a-z-]+\.pantheonsite\.io/)) {
    return configs.devPantheon;
  } else if (hostname.match(/test-[a-z-]+\.pantheonsite\.io/)) {
    return configs.testPantheon;
  } else if (hostname.match(/live-[a-z-]+\.pantheonsite\.io/)) {
    return configs.livePantheon;
  } else return configs.ddevLocal;
}

export function formatCurrencyAmount(amount, currency) {
    let formatted;
    switch (currency) {
      case 'CHF':
        formatted = `${amount.toLocaleString('de-CH')} CHF`;
        break;

      case 'EUR':
        formatted = `${amount.toLocaleString('de-DE')} EUR`;
        break;

      case 'HKD':
        formatted = `${amount.toLocaleString('en-US')} HK$`;
        break;

      case 'HRN':
        formatted = `HRN ${amount.toLocaleString('en-US')}`;
        break;

      case 'ILS':
        formatted = `${amount.toLocaleString('en-US')} ILS`;
        break;

      case 'USD':
        formatted = `USD ${amount.toLocaleString('en-US')}`;
        break;

      default:
        formatted = amount.toString();
    }

    return formatted;
}