import React, { useState, useEffect } from 'react';
import {Box, FormControl, FormLabel, Input, Button, Text, Heading, useToast,
  Alert, AlertDescription, AlertIcon } from '@chakra-ui/react';
import { formatCurrencyAmount, getConfigByHostname } from './utils/config';

const MyCredit =  ({ accessToken, userUuid, saleUuid, currency }) => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const toast = useToast();
  const config = getConfigByHostname();
  const [isLoading, setIsLoading] = useState(true);
  const [creditInfo, setCreditInfo] = useState(null);
  const [amountInput, setAmountInput] = useState(creditInfo?.amount || '');
  const [approvedAmount, setApprovedAmount] = useState(false);


  // Fetch credit info.
  useEffect(() => {
    const fetchCreditInfo = async () => {
      try {
        const response = await fetch(`${config.backendUrl}/api/credit_requests?user_uuid=${userUuid}&sale_uuid=${saleUuid}&access_token=${accessToken}`);
        // const responseData = await response.json();
        if (!response.ok) {
          // In most case it means the sale is finished.
          return;
        }
        const data = await response.json();

        // No credit request yet, show form.
        if (data.count === 0) {
          return;
        }
        // One credit request, show status.
        if (data.count === 1) {
          setCreditInfo(data.data[0]);
          setAmountInput(data.data[0].amount);
          setApprovedAmount(false);
        }

        // We have 2 credit requests, one pending and one approved.
        // latest is pending, so we want the state of the app to be pending so
        // we can submit changes to the pending request.
        if (data.count === 2) {
          setCreditInfo(data.data[1]);
          setAmountInput(data.data[1].amount);
          setApprovedAmount(data.data[0].amount);
        }

      } catch (error) {
        toast({
          title: 'Error',
          description: error.title,
          status: 'error',
          duration: 9000,
          isClosable: true,
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchCreditInfo();
  }, [accessToken, userUuid, saleUuid, toast, config]);

  // No credit request yet, show form.
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    let creditRequestData = {
      amount: amountInput,
      sale_uuid: saleUuid,
      user_uuid: userUuid
    };
    // Default post (create) request.
    let method = 'POST';
    let url = `${config.backendUrl}/api/credit_requests?access_token=${accessToken}`;
    if (creditInfo && creditInfo.id && creditInfo.status !== 'approved') {
      // If we are in an approved credit request, we need to not pass the id
      // to create a new one.
      method = 'PATCH';
      url = `${config.backendUrl}/api/credit_requests/${creditInfo.id}?access_token=${accessToken}`;
      creditRequestData = {
        amount: amountInput,
      };
    }

    try {
      const response = await fetch(url, {
        method: method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(creditRequestData),
      });

      const responseData = await response.json();

      if (response.ok) {
        setCreditInfo(responseData.data[0])
        // Display success message.
        toast({
          title: 'Credit Request Submitted',
          description: `Thank you for your credit request. A member of staff will review your request shortly.
                        You have no current credit but you have a pending request for ${ formatCurrencyAmount(responseData.data[0].amount, currency)}`,
          status: 'success',
          duration: 9000,
          isClosable: true,
        });
      } else {
        // Display error message from API.
        toast({
          title: 'Error',
          description: responseData.title || 'Error submitting credit request.',
          status: 'error',
          duration: 9000,
          isClosable: true,
        });
        // throw new Error('Error submitting credit request');
      }
    } catch (error) {
      toast({
        title: 'Error',
        description: error.message || 'There was an error processing your request.',
        status: 'error',
        duration: 9000,
        isClosable: true,
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  // Form submission logic...

  if (isLoading) {
    return <Text>Loading...</Text>;
  }

  let formHeading = 'Request Credit for Online Bidding';
  let buttonLabel = 'Send request';
  let creditStatusMessage = 'No credit request yet.';
  let creditStatus = 'info';

  // Pending strings.
  if (creditInfo && creditInfo.status === 'pending') {
    formHeading = 'Pending Request Amount';
    buttonLabel = 'Change request';
    if (!approvedAmount) {
      creditStatusMessage = `Your credit request of ${formatCurrencyAmount(creditInfo.amount, currency)} is pending.`;
    }
    else {
      creditStatusMessage = `Your credit request of ${formatCurrencyAmount(creditInfo.amount, currency)} is pending. Your current credit is  ${formatCurrencyAmount(approvedAmount, currency)}.`;
    }
  }
  // Approved strings.
  if (creditInfo && creditInfo.status === 'approved') {
    creditStatus = 'success';
    formHeading = 'My Credit Limit';
    buttonLabel = 'Send new credit request';
    creditStatusMessage = `Your credit request of ${formatCurrencyAmount(creditInfo.amount, currency)} is approved.`;
  }
  // Rejected.
  if (creditInfo && creditInfo.status === 'rejected') {
    return <Alert status="error">
             <AlertIcon />
             <AlertDescription>Your credit request has been rejected.</AlertDescription>
           </Alert>;
  }

  return (
    <Box p={4} borderWidth="1px" borderRadius="lg">
      <form onSubmit={handleSubmit}>
        <Heading as="h1" size="lg" mb={4}>{formHeading}</Heading>
        <Box className="instructions" mb={4}>
          <Text size="xs">How much credit would you like to request? Once this limit is reached, you will not be able to place further bids.</Text>
          <Text size="xs">Please do not use commas or decimal points.</Text>
        </Box>
        <Alert status={creditStatus}>
          <AlertIcon />
          <AlertDescription>{creditStatusMessage || ''}</AlertDescription>
        </Alert>
        <FormControl id="credit-request" isRequired>
          <FormLabel>Credit Request Amount ({currency})</FormLabel>
          <Input
            type="number"
            min="1"
            max="2147483647"
            value={amountInput}
            onChange={(e) => setAmountInput(e.target.value)}
          />
        </FormControl>
        <Button colorScheme="blue" mt={4} isLoading={isSubmitting} type="submit">{buttonLabel}</Button>
      </form>
    </Box>
  );
}
export default MyCredit;
