import React, { useState, useEffect } from 'react';
import { getConfigByHostname, formatCurrencyAmount } from './utils/config';
import {
  Table, Thead, Tbody, Tr, Th, Td, Box, TableContainer,
  Accordion, AccordionItem, AccordionButton, AccordionPanel, AccordionIcon,
  Spinner, Link, Flex,
  Alert, AlertIcon, AlertTitle, AlertDescription,
} from '@chakra-ui/react';
import MyCredit from './MyCredit'; // Import your MyCredit component

const MyBids = () => {
  const [accessToken] = useState(localStorage.getItem('bs_access_token'));
  const [userData, setUserData] = useState(null);
  const [salesData, setSalesData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const config = getConfigByHostname();

  // Fetch user data.
  useEffect(() => {
    if (!accessToken) {
      setError('Not logged in or configuration not loaded');
      setIsLoading(false);
      return;
    }
    fetch(`${config.backendUrl}/api/me?access_token=${accessToken}`)
      .then(response => {
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        return response.json();
      })
      .then(data => {
        setUserData(data);
      })
      .catch(error => {
        setError(error.message);
        setIsLoading(false);
      });
  }, [accessToken, config]);

  // Fetch sales data.
  useEffect(() => {
    if (!userData) return;
    const saleUuids = userData.website_bidder_info.map(info => info.sale_uuid);
    Promise.all(saleUuids.map(uuid =>
      fetch(`${config.backendUrl}/api/items?placed_bids=1&sale_uuid=${uuid}&page=1&access_token=${accessToken}`)
        .then(response => {
          if (!response.ok) {
            throw new Error('Network response was not ok for sale details');
          }
          return response.json();
        })
    ))
      .then(salesResponses => {
        const updatedSalesData = salesResponses.map((response, index) => {
          const saleInfo = userData.website_bidder_info[index];
          return {
            ...response,
            sale: saleInfo,
          };
        });
        setSalesData(updatedSalesData);
        setIsLoading(false);
      })
      .catch(error => {
        setError(error.message);
        setIsLoading(false);
      });
  }, [ userData, accessToken, config]);

  if (isLoading) {
    return (
      <Flex
        width="100vw"        // Full viewport width
        height="100vh"       // Full viewport height
        justifyContent="center" // Horizontally center the spinner
        alignItems="center"     // Vertically center the spinner
      >
        <Spinner color="blue.500" size="xl" thickness="4px" />
      </Flex>
    );
  }

  if (error) return
    <Alert status='error'>
      <AlertIcon />
      <AlertTitle>Error</AlertTitle>
      <AlertDescription>{error}</AlertDescription>
    </Alert>;

  return (
    <Accordion allowMultiple="true">
      {salesData.map((saleGroup, index) => {
        // Consolidate all bids into a single array for The current sale group
        const allBids = saleGroup.data.reduce((acc, item) => {
          // Assuming each 'item' in 'saleGroup.data' contains bid and book_bid arrays
          const combinedBids = [...item.bid, ...item.book_bid].map(bid => ({
            ...bid,
            lot: item.lot // Add The lot number to each bid
          }));
          return acc.concat(combinedBids);
        }, []);

        // Skip rendering This sale group if There are no bids
        if (allBids.length === 0) {
          return null;
        }

        return (
          <div key={index}>
            <AccordionItem>
              <h2>
                <AccordionButton>
                  <Box as="span" flex='1' textAlign='left' fontSize="lg">
                    {saleGroup.sale.sale_title}
                  </Box>
                  <AccordionIcon />
                </AccordionButton>
              </h2>
              <AccordionPanel pb={4}>
                {saleGroup.sale.sale_status !== 'closed_ended' && saleGroup.sale.sale_status !== 'closed_not_started' &&(
                  <MyCredit
                    accessToken={accessToken}
                    userUuid={userData.uuid}
                    saleUuid={saleGroup.sale.sale_uuid}
                    currency={saleGroup.sale.currency}
                  />
                )}
                <TableContainer>
                  <Table variant="striped" colorScheme='telegram'>
                    <Thead>
                      <Tr>
                        <Th>Lot</Th>
                        <Th>Type</Th>
                        <Th isNumeric>Amount</Th>
                        <Th isNumeric>Bidder ID</Th>
                      </Tr>
                    </Thead>
                    <Tbody>
                      {allBids.length === 0 ? (
                        <Tr>
                          <Td colSpan="4">No bids</Td>
                        </Tr>
                      ) : (
                        allBids.map((bid, bidIndex) => (
                          <Tr key={bidIndex}>
                            <Td>
                              <Link href={`/item-uuid/${bid.uuid}`} color="blue.500" >
                                {bid.lot}
                              </Link>
                            </Td>
                            <Td>{bid.type}</Td>
                            <Td isNumeric>{formatCurrencyAmount(bid.amount, saleGroup.sale.currency)}</Td>
                            <Td isNumeric>{bid.bidder_id}</Td>
                          </Tr>
                        ))
                      )}
                    </Tbody>
                  </Table>
                </TableContainer>
              </AccordionPanel>
            </AccordionItem>
          </div>
        );
      })}
    </Accordion>
  );
}
export default MyBids;
